<template>
  <el-dialog title="" :visible.sync="dialogTabList"  @close="choseDialog">
    <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="MAC" name="first">MAC</el-tab-pane>
    <el-tab-pane label="" name="second"></el-tab-pane>
    <el-tab-pane label="" name="third"></el-tab-pane>
    <el-tab-pane label="APP" name="fourth">APP</el-tab-pane>
    <el-tab-pane label="" name="five"></el-tab-pane>
    </el-tabs>
</el-dialog>
</template>
<script>
  export default {
    name:'',
    data(){
      return{
        dialogTabList: false,
        activeName: 'first',
      }
    },
    props:["outer"],
    methods:{
      handleClick(tab, event) {
        console.log(tab, event);
      },
      choseDialog(){
        this.$emit("choseDialog",false)
      }
    },
    watch:{
      outer(newValue){
        this.dialogTabList=newValue;
      }
    }
  }

</script>



